<template>
  <div class="searchbar">
    <Filters @search="searchProducts" />
    <div class="grid">
      <ProductItem
        v-for="product in filteredProducts"
        :key="product.id"
        :product="product"
        @add-to-basket="addToBasket"
      />
    </div>
  </div>
</template>

<script>
import { computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import Filters from './Filters.vue';
import ProductItem from './ProductItem.vue';

export default {
  components: { Filters, ProductItem },
  setup() {
    const store = useStore();

    onMounted(() => {
      store.dispatch('fetchProducts');
    });

    const filteredProducts = computed(() => store.state.products.filteredProducts);

    const searchProducts = (searchTerm) => {
      store.commit('FILTER_PRODUCTS', searchTerm);
    };

    const addToBasket = (product) => {
      store.commit('ADD_TO_BASKET', product);
    };

    return { filteredProducts, searchProducts, addToBasket };
  }
};
</script>
<style>
.grid {display: flex;flex-wrap:wrap;overflow-x: scroll;height: 400px;}
.grids {display: flex;gap:30px;flex-wrap:wrap;}
.grids.searchbar > div {display: inline-flex;gap:20px;}
.grids.searchbar {
    position: fixed;
    top: 0px;
    left: 0;
    display: flex;
    right: 0;
    margin: 0px auto;
    text-align: center;
    display: block;
    background-color: #000;
    color: #fff;padding: 10px;
}
.searchbar input {
    height:20px;
    width: 30%;
    margin: 0px auto;
    padding: 10px;
    border-radius: 10px;
} 
.searchbar button {
    background-color: #ccc;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}
.searchbar button:hover {
  background-color: #d22;
  color:#fff;
  
}
</style>