<template>
  <div class="product">
    <img :src="product.thumbnail" alt="Product Image" />
    <h3>{{ product.title }}</h3>
    <p>{{ product.price }}$</p>
    <button @click="addToBasket">Add to Basket</button>
  </div>
</template>

<script>
export default {
  props: ['product'],
  emits: ['add-to-basket'],
  setup(props, { emit }) {
    const addToBasket = () => {
      emit('add-to-basket', props.product);
    };
    return { addToBasket };
  }
};
</script>
