<template>
  <div>
    <input v-model="search" @input="updateSearch" placeholder="Type Search Any Product here" />
  </div>
</template>

<script>
import { ref } from 'vue';

export default {
  emits: ['search'],
  setup(_, { emit }) {
    const search = ref('');

    const updateSearch = () => {
      emit('search', search.value);
    };

    return { search, updateSearch };
  }
};
</script>
