<template>
  <div class="grids searchbar">
    <span>Basket</span>
    <div v-for="item in basket" :key="item.id">
      <p>{{ item.title }} - {{ item.quantity }} x ${{ item.price }}</p>
      <button @click="removeItem(item.id)">Remove</button>
    </div>
    <span>Total: ${{ total }}</span>
  </div>
</template>

<script>
import { computed } from 'vue';
import { useStore } from 'vuex';

export default {
  setup() {
    const store = useStore();
    const basket = computed(() => store.state.basket.basket);

    const removeItem = (id) => {
      store.commit('REMOVE_FROM_BASKET', id);
    };

    const total = computed(() => {
      return basket.value.reduce((sum, item) => sum + item.price * item.quantity, 0);
    });

    return { basket, removeItem, total };
  }
};
</script>
